@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);


header {
    background: var(--primaryColor);
    width: 100%;
    height: 10vh;
    padding: 1rem 2rem;
    display: flex;
    flex-wrap: wrap;
    border-bottom: 2px solid var(--secondaryColor);
    transition: .5s;
}

h3 {
    font-size: var(--font-xl);
    color: var(--fontColor);
    font-weight: 700;
    width: 20%;
    display: flex;
    height: 100%;
    align-items: center;
    transition: .2s;
}

span {
    display: flex;
    width: 80%;
    height: 100%;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
}

.nav-item {
    color: var(--fontColor);
    font-size: var(--font-n);
    text-transform: uppercase;
    display: inline-block;
    align-items: center;
    padding: 5px 15px;
    transition: .5s;
}

h3:hover {
    cursor: pointer;
    text-shadow: 0px 0px 13px var(--alternateColor);
    transition: .2s;
}

.nav-item:hover {
    text-shadow: 0px 0px 13px var(--altColor);
    transition: .2s;
    border-bottom: 2px solid var(--fontColor);
}

.icons {
    display: none;
}

@media only screen and (max-width: 600px) {


    header {
        position: fixed;
        bottom: 0;
        left: 0;
        height: 10vh;
        border-radius: 30% 30% 0 0;
        box-shadow: 0px 0px 8px var(--altColor);
        padding: 0;
        border-top: 2px solid var(--secondaryColor);
        z-index: 9999;
    }

    h3 {
        display: none;
    }

    span {
        display: flex;
        width: 100%;
        height: 100%;
        padding: 1rem 2rem;
        justify-content: space-between;
        align-items: center;
        overflow: auto;
    }

    .nav-item {
        font-size: var(--font-xs);
        width: 150px;
        border-right: 2px solid var(--secondaryColor);
    }
    .icons {
        display: block;
        font-size: large;
    }

    .nav-item:hover {
        text-shadow: 0px 0px 13px var(--altColor);
        transition: .2s;
        border-bottom: unset;
    }
    
}
main {
    background: var(--primaryColor);
    box-sizing: border-box;
    width: 100%;
    /* height: 90vh; */
    color: var(--fontColor);
    padding: 10px;
    padding-top: 5rem;
}

@media only screen and (max-width: 600px) {
    main {
        padding-bottom: 6rem;
    }
}
.cards-area {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin: 15px auto;
    justify-content: center;
    align-items: center;
}

.cards {
    position: relative;
    width: 40%;
    margin: 15px;
    height: 250px;
    border-radius: 24px;
    cursor: pointer;
    border: 1px inset var(--alternateColor);
}
.cards:hover {
    box-shadow: 0px 0px 13px var(--alternateColor);
}

.cards p {
    width: 40%;
    color: var(--fontColor);
    position: absolute;
    top: 10%;
    left: 30%;
    text-align: center;
    font-size: var(--font-l);
    text-transform: uppercase;
    text-shadow: 0px 0px 3px var(--primaryColor);
}

.cards img {
    width: 100%;
    height: 100%;
    border-radius: 24px;
}

@media only screen and (max-width: 600px) {
    .cards-area {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        margin: 15px auto;
        justify-content: center;
        align-items: center;
    }
    
    .cards {
        position: relative;
        width: 75%;
        margin: 15px;
        height: 150px;
        border-radius: 24px;
        cursor: pointer;
        border: 1px inset var(--alternateColor);
    }
}
.subtitle {
    display: block;
    text-align: center;
    margin-bottom: 3vh;
}

p {
    text-align: justify;
    width: 80%;
    margin: 0 auto;
}

@media only screen and (max-width: 600px) {
    .subtitle {
        border-bottom: 3px solid var(--secondaryColor);
    }
}
