    display: block;
    text-align: center;
    width: 100%;
    margin-bottom: 3vh;
}

p {
    text-align: justify;
    width: 80%;
    margin: 0 auto;
}
.list-group {
    margin: 15px auto;
    padding: 1rem;
    background: var(--secondaryColor);
    width: 80%;
    max-width: 400px;
    border-radius: 24px;
}

.list-title {
    border-bottom: 2px solid var(--primaryColor);
    font-size: var(--font-n);
    text-shadow: 0px 0px 8px black;
}

.list-item {
    display: flex;
    align-items: center;
    width: 80%;
    margin: 0 auto;
}

@media only screen and (max-width: 600px) {
    .list-group {
        width: 80%;
    }
}
* {
    margin: 0;
    padding: 0;
    font-size: 10px;
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    text-decoration: none;

    --primaryColor: #3d3d3d;
    --secondaryColor: #4b4b4b;
    --alternateColor: #ffaf40;
    --altColor: #c0392b;
    --fontColor: #d2dae2;
    --fontColorAlt: #808e9b;

    --font-xs: 1.2rem;
    --font-s: 1.6rem;
    --font-n: 1.8rem;
    --font-l: 2.2rem;
    --font-xl: 2.6rem;
}

a {
    outline: none;
}




.desktop {
    display: block;
}
.desktop-flex {
    display: flex;
}

.mobile {
    display: none;
}

.desktop-none {
    display: none;
}


@media only screen and (max-width: 600px) {

    .mobile-none {
        display: none;
    }
    
    .mobile {
        display: block;
    }
    
    .mobile-flex {
        display: flex;
        flex-wrap: wrap;
    }
}
